import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CustomUserFormService } from '../../http/generic-custom-form.service';
import { Store } from '@ngrx/store';
import { State } from 'src/app/reducers';
import { LoadCustomizations } from '../../ngrx-store/customization.actions';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import * as moment from 'moment';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import * as $ from 'jquery';


@Component({
  selector: 'app-generic-custom-list',
  templateUrl: './generic-custom-list.component.html',
  styleUrls: ['./generic-custom-list.component.scss']
})
export class GenericCustomListComponent extends BaseGrid implements OnInit {
  // getDataList(): void {
  //   throw new Error("Method not implemented.");
  // }
  public mobjGridSettings: GridSettings = new GridSettings();
  public mbloadingEnabled: boolean= false;
  public mbShowDeleteDialog: boolean = false;
  public screenName: string = '';
  public gridDataList: Array<any> = [];
  public coloumnNames: Array<String> = [];
  public primaryKey: any = ' ';
  public bmeDateFormat: string;
  public bmeTableName: string;
  public msHowToVideoUrl: string = '';
  public mPrimaryKeyArray: Array<any> = [];
  public msKeyword: string = '';
  public resData = {};
  public msDbTableName : string =''; //27/nov/2020
  public rowIndex : number = 0;
  public lineItem : any;
  public urlSafe: SafeResourceUrl;
  public msScreenType='';
  public msUrlPath='';
 
  constructor(private activatedRoute: ActivatedRoute, 
    private service: CustomUserFormService,
    private store: Store<State>, 
    private lsService: LocalStorageService,
    private route: Router,
    private dtService: DateFormatService,
    public dateFormatService: DateFormatService, 
    public objToastService: ToastMessageService,
    public  objUtil: AppUtility,
    public userFieldService: UserFieldService,
   
    public sanitizer: DomSanitizer) {
      super(lsService,dateFormatService, userFieldService, objToastService, objUtil);
  }

  ngOnInit() {
    window.console.log = () => { }
    this.bmeDateFormat = this.dtService.getDateFormat();
    this.activatedRoute.params.subscribe(para => {
      this.screenName = para['name'];
      this.bmlfGetHowToVideoURL();
      this.getDataList()
      this.utilService.getBMEScreePermission(this.screenName);
      this.bmlfsetExportData({ 'pageName': 'Customization' });
      localStorage.setItem('CustBackUrl',(this.route.url).replace(/%20/g, ' '))
      let menuName = localStorage.getItem('menuName');
      let parrScreenPermission = JSON.parse(localStorage.getItem("BMEScreePermission"));
      let parrFilteredData=parrScreenPermission.filter(item=>item.MenuItem_Name==menuName);
      if(parrFilteredData != undefined){
        this.bmlfGetScreenType(parrFilteredData[0].MenuItem_ID)
      }
  
    })
   
  }

  bmlfGetScreenType(screenID){
    this.mbloadingEnabled=true;
    let pobjReqData = {
      ScreenName: 'Customized Screen Master',
      LookUpKey: 'HeaderData',
      Filter1: screenID,
      
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length != undefined && res.LookupData.length > 0) {
          this.msUrlPath=res.LookupData[0].ExePath;
          if(!this.msUrlPath.includes('http')){
            this.msUrlPath = 'https://' + this.msUrlPath;
          }
          this.msScreenType=res.LookupData[0].Type;
          if(this.msScreenType=='W2'){
            this.urlSafe= this.sanitizer.bypassSecurityTrustResourceUrl(this.msUrlPath);
            this.mbloadingEnabled=false;
         }
      }
    });
  }

  /*-----Set Primarykey ---*/
  bmlfSetPrimeryKey(lineItem,pstype){
    var obj = {};
    var myArray =[]
    this.mPrimaryKeyArray.forEach((element , i)=> {
      obj[element] = lineItem[this.mPrimaryKeyArray[i]];
    });
      myArray.push(obj);
      if(pstype =='Delete'){
        return myArray;
      }else{
        return myArray[0];
      }
  }

  /*-----Get Listing Data ---*/
  getDataList() {
    this.mbloadingEnabled = true;
    this.resData = {} ; 
    this.service.getCustomFormData(this.screenName, '').subscribe(res => {
      if (res != null) {
        if (res.Response[0].ErrorCode == '') {
          this.msDbTableName = res.Response[0]['DbTableName'];
          this.mPrimaryKeyArray = [];
          res.HeaderSchema.forEach(data => {
            Object.keys(data).forEach(key => {
              if (key == 'PrimaryKey' && data[key] == true) {
                this.mPrimaryKeyArray.push(data['ColumnName'])
              }
            })
          })

          if (res.ListData.length > 0) {
            var PrimaryKey
            res.HeaderSchema.forEach(element => {
              if(element.PrimaryKey){
                 PrimaryKey = element.ColumnName;
               }
            });
            res.ListData.forEach(function(item){ delete item.Document });
            res.ListData.sort((a, b) => (a[PrimaryKey] > b[PrimaryKey] ? -1 : 1))
            res.ListData.forEach(ele => {
              Object.keys(ele).forEach(key => {

                var mpCheckCol =   res.HeaderSchema.filter(x => x['DataType'] == "time");
                if(mpCheckCol.length !=0){
                    if(key  == mpCheckCol[0].ColumnName){
                      ele[key] = moment(ele[key]).format('h:mma');
                  }
                }
              
              
              
                 if (moment(ele[key], moment.ISO_8601, true).isValid() == true && !this.bmlfisNumericCheck(ele[key])) {
                        if(ele[key].includes('-') && ele[key].includes(':') && ele[key].includes('T')){
                          ele[key] = moment(ele[key]).format(this.bmeDateFormat.toUpperCase());
                      }
                    }
                    if (key.includes("FileName")) {
                      var DeleteKey = key.replace('FileName', '');
                      delete ele[DeleteKey]; 
                    }
                })
              });

           // let listData = res.ListData;
            // listData.forEach(element => {
            //   Object.keys(element).forEach(key => {
            //     if (moment(element[key], moment.ISO_8601, true).isValid() == true && !this.bmlfisNumericCheck(element[key])) {
            //         if(element[key].includes('-') && element[key].includes(':') && element[key].includes('T')){
            //          element[key] = moment(element[key]).format(this.bmeDateFormat.toUpperCase());
            //       }
            //     }
            //   })
            // });
            if (this.mPrimaryKeyArray.length > 0) {
              this.primaryKey = this.mPrimaryKeyArray[0];
            } else {
              this.primaryKey = '';
            }
            // listData.forEach(data => {
            //   Object.keys(data).forEach(key => {
            //     if (key.includes("FileName")) {
            //       var DeleteKey = key.replace('FileName', '');
            //       delete data[DeleteKey]; 
            //     }
            //   })
            // })
            this.gridDataList = res.ListData;
            let parrSettingData = "";
            if (res.GridSetting.length > 0) {
                parrSettingData = res.GridSetting[0].JSONString;
            }
            this.setInitGridSettings(parrSettingData, this.screenName);
            this.exportData.columnsConfig = this.mobjGridSettings.columnsConfig;
            this.mobjGridSettings.state.take=50;
            this.coloumnNames = Object.keys(res.ListData[0]);
            this.resData = res;
            this.store.dispatch(new LoadCustomizations(res));
            this.mbloadingEnabled = false;
          }
          else {
           this.gridDataList = [] ; 
            this.mbloadingEnabled = false;
          }
        }
        else {
          this.gridDataList = [] ; 
          this.mbloadingEnabled = false;
        }
      }
      else {
        this.gridDataList = [] ; 
        this.mbloadingEnabled = false;
      }

    });
  }

  /*-----Set Edit ---*/
  bmlfonEdit(lineItem) {
    this.bmlfonAddlocalStoage();
    this.route.navigate(['/main/customization/custom', { paraName: "GET" ,primaryKey: [...this.mPrimaryKeyArray]} ], { queryParams: this.bmlfSetPrimeryKey(lineItem,'Edit')});
  }

  /*-----Set Add ---*/
  bmlfonAddCustom() {

    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
  };
    this.bmlfonAddlocalStoage();
    this.store.dispatch(new LoadCustomizations(this.resData));
    this.route.navigate(['/main/customization/custom', { paraName: 'Add', primaryKey: [...this.mPrimaryKeyArray] }])
  }

  /*-----Set Delete ---*/
  bmlfonDelete(lineItem, rowIndex) {

    if (!this.utilService.mbDelete) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    this.lineItem = lineItem;
    this.rowIndex = rowIndex;
    this.mbShowDeleteDialog = true;
  }

  /*-----Set Delete validation ---*/
  bmlfonDialogAction(action) {
    if (action == 'No') {
        this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
        let temp = [...this.gridDataList]
        this.service.deleteCustomForm(this.screenName, this.bmlfSetPrimeryKey(this.lineItem,'Delete') , '').subscribe(res => {
        if (res != null) {
          if (res.Response[0].ErrorCode == '') {
            this.objToastService.notifySuccess("Record Deleted.");
            temp.splice(this.rowIndex, 1);
            this.gridDataList = temp ;       
            this.mbShowDeleteDialog = false;
          }else {
            this.objToastService.notifyError("Server Error.");
            this.mbShowDeleteDialog = false;
          }
        }
      }) 
    }
  }

  
  bmlfsetExportData(data) {
    this.exportData = {  "pageName": data.pageName }
  }

  bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings(this.screenName, this.screenName);
  }

  bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid(this.screenName);
  }

  bmlFilterSetting(){
    this.filterEnabled = !this.filterEnabled;
  }



  bmlfisNumericCheck(value) {
    return /^-?\d+$/.test(value);
  }

  bmlfonAddlocalStoage(){
      localStorage.setItem('Custom', JSON.stringify({'parentscreenName': this.screenName,'tableName' : this.msDbTableName}));
  }


  closeIFrame(){
    $('#iframeCustom').remove();
}

bmlfGetHowToVideoURL(){
  let pobjReqData = {
    ScreenName: 'Custom master form',
    LookUpKey: 'GetData',
    Filter1: this.screenName
  }
  this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
    if (res.LookupData.length > 0) {
      if(res.LookupData[0].HowToVideo != ""){
        this.msHowToVideoUrl = res.LookupData[0].HowToVideo;
        localStorage.setItem('HowToUrl',this.msHowToVideoUrl);
      }
      else {
        this.msHowToVideoUrl = '';
        localStorage.setItem('HowToUrl',this.msHowToVideoUrl);
      }
    }

  })
}
}
