import { Action } from '@ngrx/store';
import *  as rootState from '../../../reducers/index'
import { CustomizationActionTypes } from './customization.actions';

export const customizationFeatureKey = 'customization';

export interface CustomState {
  customMainData: any
}

export const initialState: CustomState = {
  customMainData: {}
};

export function reducer(state = initialState, action): CustomState {
  switch (action.type) {
    case CustomizationActionTypes.LoadMainCustomizations:
      return Object.assign({}, state, { customMainData: action.payload })
    default:
      return state;
  }
}

export interface state extends rootState.State {
  'CommonReportData' : CustomState
}