 ngOnInit() {

     /*--------- get screen permission --------*/
     this.utilService.getBMEScreePermission(this.msScreenName);
     /**--- Get default date format from service--- */
     this.msCustomDateFormat = this.dateFormatService.getDateFormat();
     this.mobjUserData = this.lsService.getUserSession();
     let psScreenID = this.route.snapshot.queryParamMap.get('ScreenID');
     let psExePath=this.route.snapshot.queryParamMap.get('ExePath');
     let msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');
     // this.mobjUdfMasterHeaderModel.Status ="New";
     if (CustomValidator.bmgfNullCheckValidator(psScreenID)) {
         this.msFormMode = "I";
         this.mbCustomControl = false; // Customization
         this.msPrdKey = "";
         this.mskeyValue = this.msPrdKey;
         let pobjCopyData = localStorage.getItem('copyCustomData');
         
         if (pobjCopyData) {
             this.mstableshow = false;
             let pobjGetCopyData: any = JSON.parse(pobjCopyData);
             this.mObjModel = Object.assign({}, pobjGetCopyData.HeaderData);
             this.mObjModel.Screen_Key='';
             if(this.mObjModel.Type=='Web in New Tab'){
                this.msType=='W1'
             } 
             else{
this.msType='W2'
             }
             
           // this.mObjModel.ExePath='';
             this.mArrGrid=pobjGetCopyData.DetailData;
             if(this.mArrGrid.length>0){
                this.mArrGrid.forEach(ele=>{
                    ele.Flag='A'
                })
             }
             this.msCopyRecord = "Y";
             this.mskeyValue = pobjGetCopyData.prdKey;
             localStorage.removeItem('copyCustomData');
         }
         setTimeout(() => {
             document.getElementById("txtScreenId").focus();
         }, 200);
     } else {
        
         this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(msCopyRecord) ? "N" : msCopyRecord;
         /**----customization----- */
         this.msFormMode = this.msCopyRecord == "Y" ? "I" : "U";
         this.msPrdKey = psScreenID;
         this.msExePath=psExePath;
         this.mskeyValue = this.msPrdKey;
         this.mstableshow = false;
         if(this.msCopyRecord=="Y"){
            this.msPrdKey=psScreenID;
         }
         this.bmlfGetCustomMasterScreen();
     }
    
     this.mobjSignatureData = {
         scrrenName: this.msScreenName,
         reasonMinLength: 10,
         reqSignAuth: 2,
         date: moment().format(this.msCustomDateFormat.toUpperCase()),
         userName: this.mobjUserData.UserId
     };
     this.bmlfCheckEsignApplicableOnForm();
 }
