 bmlfAddItemData() {
     if (this.msListInput == "") {
         this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.ENTERLISTVALUE')));
         return;
     }
     let pobjData = new ListValuesModel();
     let maxLineNo = -1;
     if (this.marrDefoultValueGridData.length == 0) {
         maxLineNo = 1;
     } else {
         const a = this.marrDefoultValueGridData.reduce(function (prev, current) {
             return (prev.Id > current.Id) ? prev : current
         })
         maxLineNo = a.Id + 1;
     }
     pobjData.Id = maxLineNo;
     pobjData.Value = this.msListInput;
     this.marrDefoultValueGridData.forEach((ele,i) => {
         if (ele.Value !=''){
             if(ele.Value.toUpperCase() == this.msListInput.toUpperCase() ) {
                 this.toastMessageService.notifyError( 'Sq. No. ' + " - " + (this.marrDefoultValueGridData.slice(-1)[0].Id)+' '+ (' has duplicate list value'));
                 this.marrDefoultValueGridData[maxLineNo].Value ='';
             }
         }
     })
     this.marrDefoultValueGridData.push(Object.assign({}, pobjData));
     this.msListInput = "";
     setTimeout(function () {
         document.getElementById('txtListInput').focus();
     }, 10);
 }
 bmlfDeleteItemData(pnumIndex: number): void {
     this.marrDefoultValueGridData.splice(pnumIndex, 1);
 }

 bmlflistModellist() {
     this.GridIndex = '';
     this.modalservice.close("listModel");
 }

