   //Validate function for Other Info popup
   bmlfValidateData() {
    
    console.log("mArrGrid", this.mArrGrid, "row index", this.mnumGridRowIndex)
    if (CustomValidator.bmgfNullCheckValidator(this.mObjModel.ParentMenu)) {
       this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.MODULENAMECANNOTBEBLANK");
       return false;
   }
    if (CustomValidator.bmgfNullCheckValidator(this.mObjModel.LeftPaneName)) {
       this.bmlfOpenErrorBlock("CUSTOMIZEDSCREENMASTER.MSG.DISPLAYNAMECANNOTBEBLANK");
       return false;
   }
   if (CustomValidator.bmgfNullCheckValidator(this.mObjModel.ExePath)) {
    this.bmlfOpenErrorBlock("CUSTOMIZEDSCREENMASTER.MSG.URLCANNOTBEBLANK");
    return false;
}
    if (this.mArrGrid.length>0 && (CustomValidator.bmgfNullCheckValidator(this.mArrGrid[this.mArrGrid.length-1]['GroupName']))) {
               this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.GROUPNAMECANNOTBEBLANK");
           return false;
   } 
  
   
   else {
       return true;
   }
}

 bmlfSaveCustomMasterScreen() {
  
  if (!this.bmlfValidateData()) {
      return;
  }
  if (this.mArrGrid.length==0) {
    this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.PLEASEADDATLEASTONEROW");
    return false;
} 
this.formMode='S';
  let parrHeaderData = [];
  let parrGridData=this.mArrGrid;
  this.mObjBlankModel.ParentMenu= this.mObjModel.ParentMenu == 'CRM1' ? 'CRM' : this.mObjModel.ParentMenu;
  this.mObjBlankModel.Type=this.msType;
  this.mObjBlankModel.LeftPaneName=this.mObjModel.LeftPaneName ? this.mObjModel.LeftPaneName : '';
  this.mObjBlankModel.HowToVideo=this.mObjModel.HowToVideo ? this.mObjModel.HowToVideo : '';
  this.mObjBlankModel.RecuserID=this.mobjUserData.UserId;
  this.mObjBlankModel.ExePath=this.mObjModel.ExePath,
  this.mObjBlankModel.Screen_Key=this.mObjModel.Screen_Key ? this.mObjModel.Screen_Key : '';
  parrHeaderData.push(this.mObjBlankModel);
  parrGridData.forEach(ele=>{
      ele.Permission='';
      ele.Read=1;
      ele.write=1;
      ele.Delete=1;
      ele.Screen_Key=this.mObjModel.Screen_Key;
      ele.Permission='';
      delete ele.RowNum;
  })
  let reqData: any = {
      JSON: JSON.stringify({
          HeaderData: parrHeaderData,
          DetailData:parrGridData,
          Response: [{
              "ResponseText": "", "ErrorCode": ""
          }]
      })
  };
  
  this.objService.mblfSaveCustomizedScreenMaster(reqData, this.msFormModeModal).subscribe(res => {
      if (res == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
      }
      if (res.Response[0].ErrorCode == "") {
          {
              this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.SAVE'));
              this.mbSaveDialogViewModal=false;
              if(res.HeaderData.length >0){
                  
                  this.mObjModel.Screen_Key=res.HeaderData[0].Screen_Key;
                  this.msPrdKey=res.HeaderData[0].Screen_Key;
                  this.msExePath=res.HeaderData[0].ExePath;
                  this.mbIsChangeData=false;
                  this.bmlfGetCustomMasterScreen();
              }
             
          }
      } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
  })
}

bmlfGetCustomMasterScreen(){
  
  let parrHeaderData=[
      {
          ExePath:this.msExePath,
          Screen_Key:this.msPrdKey ? this.msPrdKey : ''
      }
  ];
  
  let reqData: any = {
      JSON: JSON.stringify({
          HeaderData: parrHeaderData,
          Response: [{
              "ResponseText": "", "ErrorCode": ""
          }]
      })
  };
  this.objService.bmlfGetCustomizedScreenMaster(reqData).subscribe(res => {
            if(res.Response[0].ErrorCode ==""){
              if(res.USRScrHdr[0] && res.USRScrHdr[0].length !=0){
                  this.mObjModel=res.USRScrHdr[0];
                  if(this.mObjModel.Type=='W1'){
                     this.mObjModel.Type='Web in New Tab';
                     this.msType='W1';
                  }
                  if(this.mObjModel.Type=='W2'){
                    this.mObjModel.Type='Web within App';
                    this.msType='W2';
                 }
              }
              if(res.UsrScrPerm.length>0){
                  this.mArrGrid=res.UsrScrPerm;
                 
              }
              else{
                  this.mArrGrid=[];
              }
              
               if(this.msCopyRecord=='Y'){
                this.mArrGrid.forEach(ele=>{
                    if(this.formMode!='S'){
                        ele.Flag="A"
                    }
                    else{
                        ele.Flag="M"
                    }
                      
                  })
                if(this.formMode!='S'){
                    this.mObjModel.Screen_Key='';
                }
               // 
               // this.msPrdKey='';
               }
               else{
                this.mArrGrid.forEach(ele=>{
                        ele.Flag="M"
                  })
               }
              
              if(this.mObjModel.Screen_Key != null || this.mObjModel.Screen_Key != ''){
                  this.msFormModeModal='U'
              }
              else{
                  this.msFormModeModal='I'
              }
            }
        }).add(() => {
      this.mbIsLoading = false;
    })
}

  /**--------Start: Delete Functionality----- */
  bmlfonDeleteCustomMaster(rowIndex,groupName) {
      
      if(!this.utilService.mbDelete) {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
        return;
      };
      this.miGridDeleteLineCustomMaster = rowIndex;
      this.mbdeleteLineItemCustomMaster = true;
  }
  
  bmlfdeleteCustomMasterAction(psAction){
      
      if (psAction == 'No') {
          this.mbdeleteLineItemCustomMaster = false;
      } 
      else if (psAction == 'Yes') {
          if(this.mArrGrid[ this.miGridDeleteLineCustomMaster]['Flag']=='A'){
              this.mArrGrid.splice(this.miGridDeleteLineCustomMaster,1);
              this.mbdeleteLineItemCustomMaster = false;
          }
          else{
              let parrHeaderData = [];
              parrHeaderData = [{ Screen_Key: this.mObjModel.Screen_Key,GroupName:this.mArrGrid[this.miGridDeleteLineCustomMaster]['GroupName']}]
              let reqData: any = {
                  JSON: JSON.stringify({
                      HeaderData: parrHeaderData,
                      Response: [{
                          "ResponseText": "", "ErrorCode": ""
                      }]
                  })
              };
              
              this.objService.bmlfDeleteCustomizedScreenMaster(reqData).subscribe(res => {
                  if (res == null) {
                      this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
                      return false;
                  }
                  if (res.Response[0].ErrorCode == "") {
                      {
                          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.DELETE'));
                          this.mArrGrid.splice(this.miGridDeleteLineCustomMaster,1);
                        this.bmlfGetCustomMasterScreen();
                          this.mbdeleteLineItemCustomMaster = false;
                      }
                  } else {
                      this.toastMessageService.notifyError(res.Response[0].ResponseText);
                      this.mbdeleteLineItemCustomMaster = false;
                  }
              })
          }
        
      }
  }

