import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { LookupService } from 'src/app/shared/http/lookup.service';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { DragAnalyzeConstants } from '../../../constants/drag-analyze.const';
import { DragAnalyzeSetupService } from '../../../http/drag-analyze-setup.service';
import * as moment from 'moment';
import { ModalService } from 'src/app/core/services/modal.service';
import { ListValuesModel } from '../../../models/udf-master-design.model';


@Component({
  selector: 'app-drag-analyze-setup-manage',
  templateUrl: './drag-analyze-setup-manage.component.html',
  styleUrls: ['./drag-analyze-setup-manage.component.scss']
})
export class DragAnalyzeSetupManageComponent implements OnInit {


  public GridIndex:any;
  public marrDefoultValueGridData:any=[];
  public marrDefoultValue:any=[];
  public msListInput:string='';



  public mbIsLoading: boolean = false;
  public msModuleKey: string;
  public msDashboardKey: string;
  public msScreenName: string = 'BI Master';
  public mbIsChangeData: boolean = false;
  public msCustomDateFormat: string = "";
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public autoCorrect: boolean = true;
  public msFormMode: string = "I";
  public marrAggregatorList: any = DragAnalyzeConstants.garrAggregateList;
  public marrDisplayAsList: any = DragAnalyzeConstants.garrDisplayList;
  /**---Object Initialize---- */
  public mobjDragAnalyzeModel: any = { DashboardId: 0, Active: false, ModuleName: "", DashboardName: "", DashboardQuery: "", DisplayAs: "", Aggregator: "", AggregatorColumn: "", Aggerated :"",DefaultColumn: "", XAxisLabel:"", YAxisLabel:"", Users: "", RecUserID:"" };
  public mobjUserData: any = {};
  public mbShowDeleteDialog: boolean = false;
  /**---Lookup realted field--- */
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModalTitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  public msLookupField = '';
  public mbMutiSelectionLookup: boolean = false;
  public mbIsQuantityLookup: boolean = false;
  /**---END : Lookup realted field--- */
  /**-------Start--Comment Description -----*/
  public miRowIndex: number = -1;
  public mbCommentDialog: boolean = false;
  public CommentDescription: string = "";
  public msCommentField: string = "";
  public msCommentMaxlength: number = 1000;
  public msCommentTitle: string = "";
  /**-------END--Comment Description -----*/
  public msCloseActionModalName: string = "";
  public mbSaveDialogView: boolean = false;
  public mbIsSearchMode: boolean = false;
  public mbIsAddMode: boolean = false;
  public mbIsCopyMode: boolean = false;
  public mbIsDeleteMode: boolean = false;
  public mbIsCloseMode: boolean = false;
  public msCopyRecord: string = "N";

  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/
  public mbQueryBuilderDialog: boolean = false;
  public mobjQueryBuilder: any = {};
  public marrSelectedUserList: any = [];
  constructor(private route: ActivatedRoute,
    private router: Router,
    private formService: DragAnalyzeSetupService,
    private lookupService: LookupService,
    private toastMessageService: ToastMessageService,
    private dateFormatService: DateFormatService,
    public utilService: AppUtility,
    private modalservice: ModalService,
    public userFieldService: UserFieldService, private lsService: LocalStorageService) {
    //this.bmlfGetDrillDownList();
  }

  ngOnInit(): void {
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default date format from service--- */
    this.mobjDragAnalyzeModel.DisplayAs = 'Bar Chart';
    //this.mobjDragAnalyzeModel.Aggregator = this.marrAggregatorList[0].ToKey;
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    let psModuleKey = this.route.snapshot.queryParamMap.get('mKey');
    let psDashboardKey = this.route.snapshot.queryParamMap.get('dKey');
    let msCopyRecord = this.route.snapshot.queryParamMap.get('CopyRecord');
    if (CustomValidator.bmgfNullCheckValidator(psModuleKey)) {
      this.msFormMode = "I";
      //this.mbCustomControl = true; // Customization
      this.msModuleKey = "";
      this.msDashboardKey = "";
      //this.mskeyValue = this.msItemKey + this.msCalendarKey;
      let pobjCopyData = localStorage.getItem('copyDashboardSetupData');
      if (pobjCopyData) {
        let pobjGetCopyData: any = JSON.parse(pobjCopyData);
        this.mobjDragAnalyzeModel = Object.assign({}, pobjGetCopyData.HeaderData);
        this.msCopyRecord = "Y";
        //this.mskeyValue = pobjGetCopyData.ForecastKey;
        localStorage.removeItem('copyDashboardSetupData');
      }
      setTimeout(() => {
        document.getElementById("txtModuleName").focus();
      }, 200);

    } else {
      this.msCopyRecord = CustomValidator.bmgfNullCheckValidator(msCopyRecord) ? "N" : msCopyRecord;
      /**----customization----- */

      this.msFormMode = this.msCopyRecord == "Y" ? "I" : "U";
      this.msModuleKey = psModuleKey;
      this.msDashboardKey = psDashboardKey;
      //this.mskeyValue = this.msItemKey + this.msCalendarKey;
      this.bmlfGetDragAnalyzeData();
    }
  }
  bmlfGetDragAnalyzeData(psInitialStage = true) {
    this.mbIsLoading = true;
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: "Search",
      Filter1: "",
      Filter2: this.msModuleKey,
      Filter3: this.msDashboardKey,
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null) {
          let pobjTempData = Object.assign({}, res.LookupData[0]);
          this.mobjDragAnalyzeModel.Active = pobjTempData.Status == "A" ? true : false;
          this.mobjDragAnalyzeModel.DashboardName = pobjTempData.DashboardName;
          this.mobjDragAnalyzeModel.DashboardQuery = pobjTempData.DashboardQuery;
          this.mobjDragAnalyzeModel.ModuleName = pobjTempData.ModuleName;
          this.mobjDragAnalyzeModel.Users = pobjTempData.Users;
          if (!CustomValidator.bmgfNullCheckValidator(pobjTempData.Users)) {
            this.marrSelectedUserList = pobjTempData.Users.split("|");
          } else {
            this.marrSelectedUserList = [];
          }
          this.mobjDragAnalyzeModel.Aggerated = pobjTempData.Aggerated;
          this.mobjDragAnalyzeModel.AggregatorColumn = pobjTempData.mvals;
          this.mobjDragAnalyzeModel.DefaultColumn = pobjTempData.mrows;
          this.mobjDragAnalyzeModel.DisplayAs = pobjTempData.rendererName;
          this.mobjDragAnalyzeModel.DashboardId = pobjTempData.DashboardId;
          this.mobjDragAnalyzeModel.XAxisLabel = pobjTempData.XAxisLabel;

          this.mobjDragAnalyzeModel.YAxisLabel = pobjTempData.YAxisLabel;

          if (this.msCopyRecord == "Y") {
            this.mobjDragAnalyzeModel.DashboardName = "";
            this.mobjDragAnalyzeModel.ModuleName = "";
            document.getElementById("txtModuleName").focus();
          }
        }
      }
    ).add(() => {
      this.mbIsLoading = false;
    })
  }
  bmlfOnFormFieldChange() {
    this.mbIsChangeData = true;
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }
  bmlfGetUserData(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.msLookupField = "";
      this.mbIsLoading = false;
      return false
    }
    let pobjReqData = {
      ScreenName: 'DragNAnalyseSetupComponent',
      LookUpKey: 'UserID',
      Filter1: this.mobjUserData.CmpDbName
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        this.mbMutiSelectionLookup = true;
        this.mbIsQuantityLookup = true;
        if (!CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.Users)) {
          let parrSplitUser = this.mobjDragAnalyzeModel.Users.split("|");
          if (parrSplitUser.length > 0) {
            res.LookupData.forEach(element => {
              let parrFilterData = parrSplitUser.filter(x => x == element.User_ID);
              if (parrFilterData.length > 0) {
                element.checked = true;
              }
            });
          }
        }
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        // if (psValue) {
        //   this.mobjLookupKeyData.searchValue = psValue.toString();
        // } else {
        this.mobjLookupKeyData.searchValue = '';
        //   }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    })
  }
  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, psTitle = "", pnumRowIdx = null) {
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psTitle != "") {
      this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    } else {
      this.mobjLookupKeyData.title = psTitle;
    }
    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.mbIsLoading = false;
      this.msLookupField = '';
      return false;
    }
    let parrFilter1 = "";

    let psReqData = {
      ScreenName: psScreenName, LookUpKey: psLookUpKey, Filter1: parrFilter1, Filter2: "", Filter3: "", Filter4: '', Filter5: ''
    };

    this.lookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }
          });
        })
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }

      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    })
  }
  /** save lookup field data */
  bmlfSaveLookupField(pobjEvent) {
    this.mbIsChangeData = true;
    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    if (pobjEvent.type == 'custom') {
      if (pobjEvent.field == "DragAnalyzeSetupList") {
        this.bmlfOpenSelectedRecord(pobjEvent.value);
      } else if (pobjEvent.field == "Users") {
        this.mbMutiSelectionLookup = false;
        this.mbIsQuantityLookup = false;
        if (pobjEvent.value.length > 0) {
          this.mobjDragAnalyzeModel.Users = pobjEvent.value[0].User_ID;
          if (pobjEvent.value.length > 1) {
            for (let i = 1; i < pobjEvent.value.length; i++) {
              this.mobjDragAnalyzeModel.Users += "|" + pobjEvent.value[i].User_ID
            }
          }
        } else {
          this.mobjDragAnalyzeModel.Users = "";
        }
        if (!CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.Users)) {
          this.marrSelectedUserList = this.mobjDragAnalyzeModel.Users.split("|");
        } else {
          this.marrSelectedUserList = [];
        }
      } else {
        this.mobjDragAnalyzeModel[pobjEvent.field] = pobjEvent.valueKey;
      }
    }
  }
  bmlfGetInputColumn(psfield) {
    if (psfield == 'ModuleName') {
      return 'ModuleName'
    } else if (psfield == 'Users') {
      return "User_ID"
    }
  }
  /**---------------Close lookup field------------- */
  bmlfCloseLookupModal(pobjEvent): void {
    this.mbMutiSelectionLookup = false;
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    if (pobjEvent.eleId != null && pobjEvent.field != 'Users') {
      let psInputKey = this.bmlfGetInputColumn(pobjEvent.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        parrFilterData = pobjEvent.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjEvent.searchValue.toUpperCase());
      }
      if (parrFilterData.length == 0) {
        if (document.getElementById(pobjEvent.eleId) != undefined) {
          document.getElementById(pobjEvent.eleId)['value'] = "";
        }

        if (this.mobjDragAnalyzeModel[pobjEvent.field] != undefined) {
          this.mobjDragAnalyzeModel[pobjEvent.field] = '';
          this.mobjDragAnalyzeModel[pobjEvent.field + "Desc"] = '';
          setTimeout(function () { document.getElementById(pobjEvent.eleId).focus(); }, 10);
        }
      }
    }
    let elementExists = document.getElementById(pobjEvent.eleId);
    if (elementExists) {
      document.getElementById(pobjEvent.eleId).focus();
    }
  }
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }
  bmlfGetStandardSql() {
    this.mbIsLoading = true;
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: "GetStandardSQL",
      Filter1: this.mobjDragAnalyzeModel.DashboardId == 0 ? "" : this.mobjDragAnalyzeModel.DashboardId
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.LookupData.length > 0) {
          this.mbIsChangeData = true;
          this.mobjDragAnalyzeModel.DashboardQuery = res.LookupData[0].StandardQuery;
        } else {
          this.bmlfOpenErrorBlock("COMMON.MSG.NODATA")
        }
      })

  }
  bmlfValidData() {
    if (CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.ModuleName)) {
      this.bmlfOpenErrorBlock("DRAGANALYZESETUP.MSG.MODULENAMEREQ");
      return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.DashboardName)) {
      this.bmlfOpenErrorBlock("DRAGANALYZESETUP.MSG.DASHBOARDNAMEREQ");
      return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.Aggerated)) {
      this.bmlfOpenErrorBlock("DRAGANALYZESETUP.MSG.DASHBOARDMEASUREMANT");
      return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.XAxisLabel)) {
      this.bmlfOpenErrorBlock("DRAGANALYZESETUP.MSG.DASHBOARDXIS");
      return false;
    } else if (CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.YAxisLabel)) {
      this.bmlfOpenErrorBlock("DRAGANALYZESETUP.MSG.DASHBOARDNYIS");
      return false;
    } else {
      return true;
    }
  }
  bmlfCheckDuplicateKey() {
    this.mbIsLoading = true;
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: "Search",
      Filter1: "",
      Filter2: this.mobjDragAnalyzeModel.ModuleName,
      Filter3: this.mobjDragAnalyzeModel.DashboardName,
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res != null) {
          if (res.LookupData.length > 0) {
            this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.EXIST'));
          } else {
            this.bmlfSaveDragAnalyzeSetupData();
          }
        } else {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
        }
      }).add(() => { this.mbIsLoading = false; });
  }
  bmlfSubmitData(pbClose: boolean = false): void {

    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (this.mbIsChangeData == false && (this.msFormMode == 'U')) {
      return;
    }
    if (!this.bmlfValidData()) {
      return;
    }
    if (this.msFormMode == "I") {
      this.bmlfCheckDuplicateKey()
    } else {
      this.bmlfSaveDragAnalyzeSetupData();
    }
  }
  bmlfSaveDragAnalyzeSetupData(): void {
    this.mbIsLoading = true;
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: this.msFormMode == "I" ? "Insert" : "Update",
      Filter1: this.mobjDragAnalyzeModel.Active ? "A" : "I",
      Filter2: this.mobjDragAnalyzeModel.ModuleName,
      Filter3: this.mobjDragAnalyzeModel.DashboardName,
      Filter4: this.mobjDragAnalyzeModel.DashboardQuery,
      Filter5: this.mobjDragAnalyzeModel.DisplayAs,
      Filter6: this.mobjDragAnalyzeModel.Aggerated,
      Filter7: this.mobjDragAnalyzeModel.XAxisLabel,
      Filter8: this.mobjDragAnalyzeModel.YAxisLabel,
      Filter9: this.mobjDragAnalyzeModel.Users,
      Filter10: this.mobjUserData.UserId

    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(
      res => {
        this.mbIsLoading = false;
        if (res.Response[0].ErrorCode == "") {
          this.mbIsChangeData = false;
          if (this.msFormMode == "I") {
            this.msFormMode = "U";
            this.msCopyRecord = "N";
            this.msModuleKey = this.mobjDragAnalyzeModel.ModuleName;
            this.msDashboardKey = this.mobjDragAnalyzeModel.DashboardName;
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
          } else if (this.msFormMode == "U") {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.EDIT"));
          }
          if (this.mbIsSearchMode) {
            this.mbIsSearchMode = false;
            this.bmlfOpenLookupModal('DragAnalyzeSetupList', this.msScreenName, 'DragAndAnalyzeSetupList', 'click', '', 'custom', 'cmdSearch', 'DRAGANALYZESETUP.TITLE');
          } else if (this.mbIsAddMode) {
            this.mbIsAddMode = false;
            this.bmlfRefreshForm();
          } else if (this.mbIsCopyMode) {
            this.mbIsCopyMode = false;
            this.bmlfCopyData();
          } else if (this.mbIsCloseMode) {
            this.mbIsCloseMode = false;
            this.router.navigate(["/main/customization/drag-analyze-setup"]);
          } else {
            this.bmlfGetDragAnalyzeData();
          }
        } else {
          this.mbIsSearchMode = false;
          this.mbIsAddMode = false;
          this.mbIsCopyMode = false;
          this.mbIsDeleteMode = false;
          this.mbIsCloseMode = false;
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
        console.log(res);
      }
    ).add(() => { this.mbIsLoading = false; })
  }
  bmlfCloseDragAnalyzeSetup(): void {
    if (this.mbIsChangeData == true) {
      this.mbSaveDialogView = true;
      this.mbIsCloseMode = true;
    } else {
      this.router.navigate(["/main/customization/drag-analyze-setup"]);
    }
  }
  // So Footer Action finction
  bmlfFooterActionFn(event) {
    if (event == 'searchEvn') {
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsSearchMode = true
        return false;
      }
      this.bmlfOpenLookupModal('DragAnalyzeSetupList', this.msScreenName, 'DragAndAnalyzeSetupList', 'click', '', 'custom', 'cmdSearch', 'DRAGANALYZESETUP.TITLE');
    } else if (event == 'addEvn') {
      if (!this.utilService.mbWrite) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return false;
      };
      if (!this.bmlfValidData()) {
        return;
      }
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsAddMode = true
        return false;
      }
      this.bmlfRefreshForm();
    } else if (event == 'deleteEvn') {
      if (!this.utilService.mbDelete) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
        return false;
      };
      if (this.mobjDragAnalyzeModel.ModuleName == '' && this.msFormMode == "I" && this.msCopyRecord == "N" && !this.mbIsChangeData) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
        return false;
      }
      if (this.mobjDragAnalyzeModel.ModuleName == '' && this.msFormMode == "I" && this.msCopyRecord == "Y" && !this.mbIsChangeData) {
        this.bmlfRefreshForm();
        return false;
      }
      this.mbShowDeleteDialog = true;
    } else if (event == 'copyEvn') {
      if (!this.utilService.mbWrite) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
        return false;
      };
      if (CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.ModuleName) && CustomValidator.bmgfNullCheckValidator(this.mobjDragAnalyzeModel.DashboardName)) {
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate("COMMON.MSG.COPY"));
        document.getElementById("txtModuleName").focus();
        return;
      }
      if (this.mbIsChangeData) {
        this.mbSaveDialogView = true;
        this.mbIsCopyMode = true;
        return false;
      }
      this.bmlfCopyData();
    }
  }
  bmlfOnConfirmModal(psype) {
    if (psype == 'YES') {
      this.bmlfSubmitData();
      this.mbSaveDialogView = false;
    } else if (psype == 'NO') {
      this.mbSaveDialogView = false;
      if (this.mbIsSearchMode) {
        this.mbIsSearchMode = false;
        this.bmlfOpenLookupModal('DragAnalyzeSetupList', this.msScreenName, 'DragAndAnalyzeSetupList', 'click', '', 'custom', 'cmdSearch', 'DRAGANALYZESETUP.TITLE');
      } else if (this.mbIsAddMode) {
        this.mbIsAddMode = false;
        this.bmlfRefreshForm();
      } else if (this.mbIsCopyMode) {
        this.mbIsCopyMode = false;
        this.bmlfCopyData();
      } else if (this.mbIsDeleteMode) {
        this.mbIsDeleteMode = false;
        this.mbShowDeleteDialog = true;
      }
      else {
        this.router.navigate(['/main/customization/drag-analyze-setup']);
      }
    } else {
      this.mbSaveDialogView = false;
      this.mbIsSearchMode = false;
      this.mbIsCopyMode = false;
      this.mbIsAddMode = false;
      this.mbIsCloseMode = false;
    }
  }
  // Delete Transaction Type Dialog events
  bmlfonDialogAction(psAction) {
    if (psAction == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (psAction == 'Yes') {
      if (this.msFormMode == "I") {
        this.bmlfRefreshForm();
        return false;
      }
      let parrHeaderData = [];
      parrHeaderData = [{
        "DashboardId": this.mobjDragAnalyzeModel.DashboardId
      }]
      let reqData: any = {
        JSON: JSON.stringify({
          FrcstH: parrHeaderData,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.formService.bmlfDeleteDragAndAnalyse(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.DELETE'));
            this.mbShowDeleteDialog = false;
            this.bmlfRefreshForm();
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.mbShowDeleteDialog = false;
        }
      })
    }
  }
  bmlfOpenSelectedRecord(data) {
    let param = {
      mKey: data.ModuleName,
      dKey: data.DashboardName
    };
    this.utilService.bmgfRefreshReuse('/main/customization/drag-analyze-setup/manage', true, param);
  }
  // Refresh Sales Order
  bmlfRefreshForm() {
    this.utilService.bmgfRefreshReuse('/main/customization/drag-analyze-setup/manage', false);
  }
  // Copy Current WHT Data
  bmlfCopyData() {
    let pobjHeaderData = Object.assign({}, this.mobjDragAnalyzeModel);
    pobjHeaderData.ModuleName = "";
    pobjHeaderData.DashboardName = "";

    let parrCopyData = {
      HeaderData: pobjHeaderData,
      // ForecastKey: this.mobjDragAnalyzeModel.ItemKey + this.mobjDragAnalyzeModel.CalendarKey,
    }
    localStorage.setItem('copyDashboardSetupData', JSON.stringify(parrCopyData));
    this.bmlfRefreshForm();
  }
  bmlfOpenQueryWizardModal() {
    //Add for HierarchicalGridSQL  2Jun2023 --S
    this.mobjQueryBuilder.ScreenName = this.msScreenName;
    this.mobjQueryBuilder.RecordKey1 = this.mobjDragAnalyzeModel.ModuleName;
    this.mobjQueryBuilder.RecordKey2 = this.mobjDragAnalyzeModel.DashboardName;
    this.mobjQueryBuilder.FieldName = "COMMON";  
    //Add for HierarchicalGridSQL  2Jun2023 --E
    this.mobjQueryBuilder.Type = "COMMON";
    this.mobjQueryBuilder.SqlQuery = this.mobjDragAnalyzeModel.DashboardQuery;
    this.mbQueryBuilderDialog = true;
  }
  bmlfCloseQueryBuilderModal(event) {
    if (event.type == "Transfer") {
      if (event.data.SqlQuery != this.mobjDragAnalyzeModel.DashboardQuery) {
        this.mbIsChangeData = true;
      }
      this.mobjDragAnalyzeModel.DashboardQuery = event.data.SqlQuery;
    }
    this.mbQueryBuilderDialog = false;
  }
  // Description expanded Modal
  bmlfDescriptionModal(event, field, title = '') {
    if (event == 'open') {
      this.msCommentTitle = title;
      this.msCommentField = field;
      if (field == "DashboardQuery") {
        this.msCommentMaxlength = null;
      } else {
        this.msCommentMaxlength = 100;
      }
      this.CommentDescription = this.mobjDragAnalyzeModel[field];
      this.mbCommentDialog = true;
    } else if (event == 'close') {
      this.mobjDragAnalyzeModel[this.msCommentField] = this.CommentDescription;
      this.CommentDescription = "";
      this.mbCommentDialog = false;
    }
  }
  //Open Task MODAL
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mobjDragAnalyzeModel.ModuleName + "" + this.mobjDragAnalyzeModel.DashboardName;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/BI Master.pdf', '_blank');
  }



  
  /* listing code start --*/
  bmlfAddItemSql( ) {
  
    this.marrDefoultValue = [];

    if(this.mobjDragAnalyzeModel.Aggerated.toString() != "") {
        var psValuesArrary = this.mobjDragAnalyzeModel.Aggerated.toString().split('|');
        if(psValuesArrary.length > 0) {
            let maxLineNo = -1;
            for(var value of psValuesArrary) {
                var pobjDataCopy: any = Object.assign({}, this.mobjDragAnalyzeModel);
                maxLineNo = maxLineNo + 1;
                pobjDataCopy.Id = maxLineNo;
                pobjDataCopy.Value = value;
                this.marrDefoultValue.push(pobjDataCopy);
            }
        }
    }
    this.modalservice.open("listModel");
    setTimeout(function() {
        document.getElementById('txtListInput').focus();
    }, 10);

    }

    bmlflistModellist() {
    this.modalservice.close("listModel");
    }

    bmlfOK() {
    let psListValue = '';
    this.marrDefoultValue.reverse().forEach(ele => {
        psListValue = ele.Value + "|" + psListValue;
    });
    psListValue = psListValue.slice(0, -1);
    this.mobjDragAnalyzeModel.Aggerated = psListValue;
    this.mbIsChangeData = true;
    this.modalservice.close("listModel");
    }

    bmlfAdd() {
    if(this.msListInput == "") {
        this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.ENTERLISTVALUE')));
        return;
    }
    let pobjData = new ListValuesModel();
    let maxLineNo = -1;
    if(this.marrDefoultValue.length == 0) {
        maxLineNo = 1;
    } else {
        const a = this.marrDefoultValue.reduce(function(prev, current) {
            return(prev.Id > current.Id) ? prev : current
        })
        maxLineNo = a.Id + 1;
    }
    pobjData.Id = maxLineNo;
    pobjData.Value = this.msListInput;
    this.marrDefoultValue.push(Object.assign({}, pobjData));
    this.msListInput = "";
    setTimeout(function() {
        document.getElementById('txtListInput').focus();
    }, 10);
    }

    bmlfDeleteItemData(pnumIndex: number): void {
        this.marrDefoultValue.splice(pnumIndex, 1);
    }
  /* listing code End --*/


}
