import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class UdfService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteCustomMasterRecord(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteUDFMasterRecord`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveCustomMasterRecord(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/CreateUDFMasterRecord`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/UpdateUDFMasterRecord`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfSaveGeneratedTable(headerData: any,) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GenerateUDFMasterTable`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetUFDMasterRecord(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetUFDMasterRecord`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetCustoomLineDelete(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/DeleteUDFMasterLine`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfGetAliaseUDFData(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/GetAliaseUDFData`;
        return this.httpExt.post<any>(url, headerData, null);
    }

}