import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class ReportGenerationService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteCustomizedReport(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteCustomizedReport`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveCustomizedReport(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFCreateCustomizedReport`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFUpdateCustomizedReport`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfGetCustomizedReport(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetCustomizedReport`;
        return this.httpExt.post<any>(url, headerData, null);
    }


    public bmlfDeleteCustomizedReportLine(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteCustomizedReportLine`;
        return this.httpExt.post<any>(url, headerData, null);
    }


    public bmlfDeleteCustomizedReportGroupPerm(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteCustomizedReportGroupPerm`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    
    public bmgfUploadRPTFile(reqdata) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/UploadRPTFile`;
        return this.httpExt.post(url, reqdata, null);
    }

    
}