import { Injectable } from '@angular/core';
import { HttpCleintExtService } from 'src/app/core/http/http-client-ext.service';
import { AppSettingsService } from 'src/app/core/services/appsettings.service';

@Injectable({
    providedIn: 'root'
  })
export class DilldownService {

    constructor(private httpExt: HttpCleintExtService, private appSettings: AppSettingsService) {
    }
    public bmlfDeleteDrillDown(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteDrillDown`;
        return this.httpExt.post<any>(url, headerData, null);
    }
    public bmlfSaveDrillDown(headerData: any, formMode) {
        if (formMode == "I") {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFCreateDrillDown`;
            return this.httpExt.post<any>(url, headerData, null);
        } else {
            const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFCreateDrillDown`;
            return this.httpExt.post<any>(url, headerData, null);
        }
    }

    public bmlfGetDrillDownMaster(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFGetDrillDownMaster`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    public bmlfDeleteDrillDownLine(headerData: any) {
        const url = `${this.appSettings.ServerAPIEndpoint}/bme/BMGFDeleteDrillDownLine`;
        return this.httpExt.post<any>(url, headerData, null);
    }

    
}