import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { CustomizationComponent } from './customization.component';
import { GenericCustomFormComponent } from './components/generic-custom-form/generic-custom-form.component';
import { GenericCustomListComponent } from './components/generic-custom-list/generic-custom-list.component';
import { DragAnalyzeSetupListComponent } from './components/drag-analyze-setup/drag-analyze-setup-list/drag-analyze-setup-list.component';
import { DragAnalyzeSetupManageComponent } from './components/drag-analyze-setup/drag-analyze-setup-manage/drag-analyze-setup-manage.component';
import { CustomMasterDesignComponent } from './components/custom-master/custom-master-design/custom-master-design.component';
import { CustomMasterComponent } from './components/custom-master/custom-master/custom-master.component';
import { DilldownListComponent } from './components/dilldown/dilldown-list/dilldown-list.component';
import { DilldownMasterComponent } from './components/dilldown/dilldown-master/dilldown-master.component';
import { ReportGenerationListComponent } from './components/report-generation/report-generation-list/report-generation-list.component';
import { ReportGenerationMasterComponent } from './components/report-generation/report-generation-master/report-generation-master.component';
import { UdfListingComponent } from './components/udf/udf-listing/udf-listing.component';
import { UdfMasterComponent } from './components/udf/udf-master/udf-master.component';
import { CustomizedScreenMasterListComponent } from './components/customized-screen-master/customized-screen-master-list/customized-screen-master-list.component';
import { CustomizedScreenMasterManageComponent } from './components/customized-screen-master/customized-screen-master-manage/customized-screen-master-manage.component';

const routes:Routes = [
  {
      path : '',
      component : CustomizationComponent,
      children : [
          {path:'',redirectTo:'customlist',pathMatch:'full'},
          {path:'custom',component:GenericCustomFormComponent,canActivate:[AuthGuard]},
          {path:'customlist/:name',component:GenericCustomListComponent,canActivate:[AuthGuard]},
          {path:'drag-analyze-setup',component:DragAnalyzeSetupListComponent,canActivate:[AuthGuard]},
          {path:'drag-analyze-setup/manage',component:DragAnalyzeSetupManageComponent,canActivate:[AuthGuard]},
          {path:'custom-master-design',component:CustomMasterDesignComponent,canActivate:[AuthGuard]},
          {path:'custom-master-design/manage',component:CustomMasterComponent,canActivate:[AuthGuard]},
          {path:'dilldown-list', component:DilldownListComponent,canActivate: [AuthGuard]},
          {path:'dilldown-master/manage', component:DilldownMasterComponent,canActivate: [AuthGuard]},
          {path:'report-generation-list', component:ReportGenerationListComponent,canActivate: [AuthGuard]},
          {path:'report-generation/manage', component:ReportGenerationMasterComponent,canActivate: [AuthGuard]},
          {path:'udf-list', component:UdfListingComponent,canActivate: [AuthGuard]},
          {path:'udf-master/manage', component:UdfMasterComponent,canActivate: [AuthGuard]},
          {path:'custom-web-page', component:CustomizedScreenMasterListComponent,canActivate:[AuthGuard]},
          {path:'custom-web-page/manage', component:CustomizedScreenMasterManageComponent,canActivate:[AuthGuard]}
      ]
  }
]

@NgModule({
    imports:[RouterModule.forChild(routes)],
    exports:[RouterModule]
})
export class CustomizationRouting {

}
