    bmlfValidData() {
        if (CustomValidator.bmgfNullCheckValidator(this.mobjReportGenerationModul.ReportID)) {
            this.bmlfOpenErrorBlock("REPORTGEN.MSG.REPORTDI");
            return false;
        }else if (CustomValidator.bmgfNullCheckValidator(this.mobjReportGenerationModul.ParentMenu)) {
            this.bmlfOpenErrorBlock("REPORTGEN.MSG.MODULE");
            return false;
        }else if (CustomValidator.bmgfNullCheckValidator(this.mobjReportGenerationModul.LeftPaneName)) {
            this.bmlfOpenErrorBlock("REPORTGEN.MSG.LEFTPANENAME");
            return false;
        }else if (this.marrDefoultValueGridData.length == 0) {
            this.bmlfOpenErrorBlock("REPORTGEN.MSG.MAINGRID");
            return false;

        }else if ( this.marrDefoultValueGridData.filter(function(x,i) { return !x.SeqNo  }).length >0) {
            this.bmlfOpenErrorBlock("REPORTGEN.MSG.SELECTSEQNUMBER");
            return false;

        }else if ( this.marrDefoultValueGridData.filter(function(x,i) { return !x.LabelCaption  }).length >0) {
            this.bmlfOpenErrorBlock("REPORTGEN.MSG.ENTERLABEL");
            return false;

        }else if ( this.marrDefoultValueGridData.filter(function(x,i) { return !x.CtrlType  }).length >0) {
            this.bmlfOpenErrorBlock("REPORTGEN.MSG.ENTERCONTROLTYPE");
            return false;

        // }else if ( this.marrDefoultValueGridData.filter(function(x,i) { return !x.CtrlIndex  }).length >0) {
        //     this.bmlfOpenErrorBlock("REPORTGEN.MSG.ENTERCONTRALINDEX");
        //     return false;
            

        // }else if ( this.marrGroupNameGridData.length == 0 || this.marrGroupNameGridData.filter(function(x,i) { return !x.GroupName  }).length >0) {
        //     this.bmlfOpenErrorBlock("REPORTGEN.MSG.GROUPGRID");
        //     return false;
            
        } else {
            return true;
        }
    }
