     /** ---Degoult value array valication for insert line--- */
     bmlfValidItemDataSetupDetail() {
        let RowNumbers: number = 0;
        let pbResult: boolean = true;
        this.marrDefoultValueGridData.forEach(ele => {
            if(pbResult == true) {
                RowNumbers = this.marrDefoultValueGridData.slice(-1)[0].SeqNo 

                if(ele.SeqNo == "") {
                    this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('REPORTGEN.MSG.SELECTSEQNUMBER')));
                    pbResult = false;
                }else if(ele.LabelCaption == "") {
                    this.toastMessageService.notifyError('Sr. No. ' + RowNumbers +' - '+ (this.utilService.bmgfonTranslate('REPORTGEN.MSG.ENTERLABEL')));
                    pbResult = false;
                }else if(ele.CtrlType == "") {
                    this.toastMessageService.notifyError('Sr. No. ' + RowNumbers +' - '+ (this.utilService.bmgfonTranslate('REPORTGEN.MSG.ENTERCONTROLTYPE')));
                    pbResult = false;
                }
            }
        });
        return pbResult;
      }


        /** -- dubicate entry valication--- */
        bmlfvalicationDubicatecheck(pobjEvent,rowIndex) {
            let pbResult: boolean = true;
            this.marrGroupNameGridData.forEach((ele,i) => {
              if(pbResult == true) {
                if(ele.GroupName.toUpperCase() == pobjEvent.toUpperCase() && i != (rowIndex)) {
                    this.toastMessageService.notifyError( 'Sq. No. ' + " - " + (this.marrGroupNameGridData.length)+' '+ (this.utilService.bmgfonTranslate('REPORTGEN.MSG.DUPLICATEGROUPNAME')));
                    this.marrGroupNameGridData[rowIndex].GroupName ='';
                    pbResult = false;
                    }
                }
            });
            return pbResult;
          }




    
    



        /** -- Add New row for defoult value --- */
        bmlfAddItemData() {
          if(!this.mobjReportGenerationModul.ReportID){
              this.bmlfOpenErrorBlock('REPORTGEN.MSG.REPORTDI');
              return false;
          }else if(!this.mobjReportGenerationModul.ParentMenu){
              this.bmlfOpenErrorBlock('REPORTGEN.MSG.MODULE');
              return false;
          }
      
         if(this.bmlfValidItemDataSetupDetail()) {
              var SeqNo = 0
              if(this.marrDefoultValueGridData.length){
                SeqNo  = Number(this.marrDefoultValueGridData.at(-1).SeqNo) + 1 
              }else{
                SeqNo  = this.marrDefoultValueGridData.length
              }
              let pobjData = new ReportGenerationDetailModel();
              pobjData.ReportID= this.mobjReportGenerationModul.ReportID ? this.mobjReportGenerationModul.ReportID:'';
              pobjData.SeqNo = SeqNo ? SeqNo : 1;
              pobjData.LabelCaption= '';
              pobjData.SelectQuery='';
              pobjData.FieldName = '';
              this.marrDefoultValueGridData.push(Object.assign({}, pobjData));
              this.mbIsChangeData = true;
         }
      }


      bmlfValidItemData(){

        let RowNumbers: number = 0;
        let pbResult: boolean = true;
        this.marrGroupNameGridData.forEach(ele => {
            if(pbResult == true) {
                RowNumbers = this.marrGroupNameGridData.length

                if(ele.GroupName == "") { 
                    this.toastMessageService.notifyError( "Sq no"+" "+RowNumbers + ' - ' + (this.utilService.bmgfonTranslate('REPORTGEN.MSG.GROUPGRID')));
                    pbResult = false;
                }
            }
        });
        return pbResult;
      }
  


