   /**-------- Delete Functional----- */
bmlfdialogDeleteLineAction(action: any) {
    if(action == 'Yes') {
      this.mbIsChangeData = true;
      this.mbdeleteLineItem = false;
       //HeaderData: [ this.marrDefoultValueGridData[this.miGridDeleteLineIndex]],
      let reqData: any = {
        JSON: JSON.stringify({
         
          HeaderData: [{ ReportID : this.marrDefoultValueGridData[this.miGridDeleteLineIndex].ReportID , SeqNo : this.marrDefoultValueGridData[this.miGridDeleteLineIndex].SeqNo  }],
          Response: [{
            "ResponseText": "",
            "ErrorCode": ""
          }]
        })
      };
      this.formService.bmlfDeleteCustomizedReportLine(reqData).subscribe(res => {
        if(res == null) {
          return;
        }
        if(res.Response[0].ErrorCode != "") {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        } else {
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('COMMON.MSG.LINEDELETE'));
          this.marrDefoultValueGridData.splice(this.miGridDeleteLineIndex, 1);
        }
      });
    }
    if(action == 'No') {
      this.mbdeleteLineItem = false;
    }
  }
