  bmlfHandleFileUpload(files: FileList) {
    if (files.length == 0) {
      return;
    }
    const formData: FormData = new FormData();
    if (files.length) {
       this.bmlfOnFormFieldChange() 
      this.selctedRpt = files[0].name;
      formData.append('excel', files[0], files[0].name);
      this.msReportBaseFile = formData;
    } else {
      this.msReportBaseFile = '';
      (<HTMLInputElement>document.getElementById('txtSelctedRpt')).value = '';
    }
  }



