  /* listing code start --*/
    bmlfAddItemSql(rowIndex, datatype) {
    this.GridIndex = rowIndex;
    this.marrDefoultValue = [];

    if(this.marrDefoultValueGridData[this.GridIndex].CtrlType.toString() != "") {
        var psValuesArrary = this.marrDefoultValueGridData[this.GridIndex].SelectQuery.toString().split('|');
        if(psValuesArrary.length > 0) {
            let maxLineNo = -1;
            for(var value of psValuesArrary) {
                var pobjDataCopy: any = Object.assign({}, this.marrDefoultValueGridData);
                maxLineNo = maxLineNo + 1;
                pobjDataCopy.Id = maxLineNo;
                pobjDataCopy.Value = value;
                this.marrDefoultValue.push(pobjDataCopy);
            }
        }
    }
    this.modalservice.open("listModel");
    setTimeout(function() {
        document.getElementById('txtListInput').focus();
    }, 10);

    }

    bmlflistModellist() {
    this.GridIndex = '';
    this.modalservice.close("listModel");
    }

    bmlfOK() {
    let psListValue = '';
    this.marrDefoultValue.reverse().forEach(ele => {
        psListValue = ele.Value + "|" + psListValue;
    });
    psListValue = psListValue.slice(0, -1);
    this.marrDefoultValueGridData[this.GridIndex].SelectQuery = psListValue;
    this.mbIsChangeData = true;
    this.modalservice.close("listModel");
    }

    bmlfAdd() {
    if(this.msListInput == "") {
        this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.ENTERLISTVALUE')));
        return;
    }
    let pobjData = new ListValuesModel();
    let maxLineNo = -1;
    if(this.marrDefoultValue.length == 0) {
        maxLineNo = 1;
    } else {
        const a = this.marrDefoultValue.reduce(function(prev, current) {
            return(prev.Id > current.Id) ? prev : current
        })
        maxLineNo = a.Id + 1;
    }
    pobjData.Id = maxLineNo;
    pobjData.Value = this.msListInput;
    this.marrDefoultValue.push(Object.assign({}, pobjData));
    this.msListInput = "";
    setTimeout(function() {
        document.getElementById('txtListInput').focus();
    }, 10);
    }

    bmlfDeleteItemData(pnumIndex: number): void {
        this.marrDefoultValue.splice(pnumIndex, 1);
    }
  /* listing code End --*/


