    /** -- Add New row for defoult value --- */
    bmlfAddItemDetailSetup() {
        if (!this.mobjUdfMasterHeaderModel.ScreenName) {
            this.bmlfOpenErrorBlock('CUSTOMMASTERDESIGN.MSG.SELECTTABLENAME');
            return false;
        }
        var RowID = 0;
        if (this.marrSetupDetailMainList.length == 0) {
            RowID = 0
        } else {
            RowID = this.marrSetupDetailMainList.slice(-1)[0].RowID
        }
        this.mbClickAddLine = true;
        if (this.bmlfValidItemDataSetupDetail()) {
            let pobjData = new udfMasterDesignModel();
            pobjData.RowID = Number(RowID) + 1
            pobjData.ColumnNumber = 1
            pobjData.ScreenName = this.mobjUdfMasterHeaderModel.ScreenName
            pobjData.ColumnName = ''
            pobjData.Caption = ''
            pobjData.RecUserID = this.mobjUserData.UserId;
            pobjData.Flag = "A";
            pobjData.IsDislable = "false";
            this.marrSetupDetailMainList.push(Object.assign({}, pobjData));
            this.mbIsChangeData = true;
        }
    }
    bmlfOpenErrorBlock(psMsg): void {
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
    }
