    bmlfGETAliaseName() {
        if (this.msAliases) {
            let pobjOData = { HeaderData: [{ "AliaseName": this.msAliases }], Response: [{ "ResponseText": "", "ErrorCode": "" }] }
            let psJData = JSON.stringify(pobjOData);
            let psTempjData = JSON.stringify(psJData);
            let psJson = '{"JSON":' + psTempjData + '}';
            this.formService.bmlfGetAliaseUDFData(psJson).subscribe(res => {
                if (res.Response[0].ErrorCode == "") {
                    if (res.DetailData.length > 0) {
                        this.msCheckprimayKey = true;
                        if (this.msFormMode == "I") {
                            this.msIsDetail = false;
                        } else if (this.msFormMode == "U") {
                            this.msIsDetail = true;
                        }
                        res.DetailData.forEach(element => {
                            if (element.PrimaryKey) {
                                element.IsDislable = true;
                            } else {
                                element.IsDislable = false;
                            }
                        });
                    
                        this.mstableMasterName = res.HeaderData1[0].ScreenName + 'H'
                        this.mobjUdfMasterHeaderModel.dbTableName = res.HeaderData1[0].dbTableName
                        this.mobjUdfMasterHeaderModel.Status = 'GENERATED';
                        this.mbCustomControl = true;
                        this.mobjUdfMasterHeaderModel.User1 = this.msAliases
                        this.marrSetupDetailMainList = res.DetailData;
                        this.marrSetupDetailMainList.forEach(ele =>{
                            if(ele.USER11 == 1){
                                ele.User11 = 1
                            }
                        });
                        this.bmlfSoltingDetails();
                        this.msIsAliases = true;
                        this.toastMessageService.notifySuccess('Updated Grid');
                    }else{
                        this.msIsAliases = false;
                    }
                }
            })
        }
    }



