    bmlfValidData() {
        if (CustomValidator.bmgfNullCheckValidator(this.mobjUdfMasterHeaderModel.ScreenName)) {
            this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.ENTERSCREENNAME");
            return false;
        } else if (this.marrSetupDetailMainList.filter(function (x, i) {
            return x.PrimaryKey == true
        }).length == 0) {
            this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.SELECTPRIMAYKEY");
            return false;
        } else if (this.marrSetupDetailMainList.filter(function (x, i) {
            return !x.Caption
        }).length > 0) {
            this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.SELECTCAPTIONKEY");
            return false;
        } else {
            return true;
        }
    }
