import { Component, OnInit } from '@angular/core';
import { locale as english } from './i18n/en';

import { locale as Chinese } from './i18n/zh';
import { locale as French } from './i18n/fr';
import { locale as Spanish } from './i18n/es';
import { locale as Portuguese } from './i18n/pt';
import { locale as Vietnamese } from './i18n/vi';


import { LanTranslationLoaderService } from 'src/app/core/services/translation-loader.service';

@Component({
  selector: 'app-customization',
  templateUrl: './customization.component.html',
  styleUrls: ['./customization.component.scss']
})
export class CustomizationComponent implements OnInit {

  constructor(private _lanTranslationLoaderService: LanTranslationLoaderService) { }

  ngOnInit() {
    
    this._lanTranslationLoaderService.loadTranslations(english, Chinese, French, Spanish, Portuguese, Vietnamese );
  }

}
