      /** -- dubicate entry valication for Box1099 --- */
      bmlfvalicationDubicatecheck(pobjEvent,rowIndex,type) {
        let pbResult: boolean = true;
        this.mbIsChangeData = true;
        this.marrSetupDetailMainList.forEach((ele,i) => {
          if(pbResult == true) {
              if(type == 'CaptionName' && pobjEvent !=''){
                if(ele.CaptionName.toUpperCase() == pobjEvent.toUpperCase() && i != (rowIndex)) {
                    this.toastMessageService.notifyError( 'Sq. No. ' + " - " + (this.marrSetupDetailMainList.slice(-1)[0].RowID)+' '+ (this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.DUPLICATECAPNAME')));
                    this.marrSetupDetailMainList[rowIndex].CaptionName ='';
                    pbResult = false;
                  }
              }else{

               if(ele.RowID == this.marrSetupDetailMainList.slice(-1)[0].RowID && (/\s/).test(pobjEvent) ==true){
                   if((/\s/).test(pobjEvent)){
                        this.mbIsChangeData=true;
                        this.marrSetupDetailMainList[rowIndex].ColumnName ='';
                        this.toastMessageService.notifyError( 'Sq. No. ' + " - " + (ele.RowID)+' '+ (this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.SPACE')));
                        return
                    }
                }
                if(pobjEvent !='' && ele.ColumnName.toUpperCase() == pobjEvent.toUpperCase() && i != (rowIndex)) {
                  this.toastMessageService.notifyError(  'Sq. No. ' + " - " + (this.marrSetupDetailMainList.slice(-1)[0].RowID )+' '+(this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.DUPLICATECALNAME')));
                    this.marrSetupDetailMainList[rowIndex].ColumnName ='';
                    pbResult = false;
                  }
              }
          }
        });
        return pbResult;
      }
  


