  bmlfGetInputColumn(psField) {
    if (psField == 'ParentMenu') {
        return 'ModuleName';
    } else if (psField == 'GroupName') {
        return 'Group_ID';
    }
    else if (psField == 'ParameterName') {
        return 'ParaName'
    } else if(psField == 'Email' || psField == 'Value'){
        return 'COLUMN_NAME'
    }
}

//Close Lookup Modal
  bmlfCloseLookupModal(pobjData) {
    // this.mbIsLookupModal = pobjData.isLookupModal;
    //   document.getElementById(pobjEvent.eleId).focus();
   this.mbIsLookupModal = pobjData.isLookupModal;
   if (pobjData.eleId != null) {
       let psInputKey = this.bmlfGetInputColumn(pobjData.field);
       let parrFilterData: any = [];
       if (psInputKey) {
           parrFilterData = pobjData.gridData.filter(obj => obj[psInputKey].toUpperCase() == pobjData.searchValue ? pobjData.searchValue.toUpperCase() : '');
       }
       // console.log(pos);
       if (parrFilterData.length == 0) {
           this.mObjModel[pobjData.field] = '';
       }
       if (this.mnumLineItemRowIndex != null && this.mnumLineItemRowIndex >= 0) {
        this.mArrGrid[this.mnumLineItemRowIndex][pobjData.field] = "";
        setTimeout(function () { document.getElementById(pobjData.eleId).focus(); }, 10);
        return;
      }
       if (this.mobjLookupKeyData[pobjData.field] != undefined) {

           this.mobjLookupKeyData = {};
           
           setTimeout(function () { document.getElementById(pobjData.eleId).focus(); }, 10);
       }
           document.getElementById(pobjData.eleId).focus();
       let elementExists = document.getElementById(pobjData.eleId);
       if (elementExists) {
           document.getElementById(pobjData.eleId).focus();
       }
   }
}
