  //Validate function for Other Info popup
  bmlfValidateData() {
     if (CustomValidator.bmgfNullCheckValidator(this.mObjModel.ParentMenu) && !this.mbIsDisabledModule) {
        this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.MODULENAMECANNOTBEBLANK");
        return false;
    }
     if (CustomValidator.bmgfNullCheckValidator(this.mObjModel.LeftPaneName) && !this.mbIsDisabledLeftPane) {
        this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.LEFTPANNAMECANNOTBEBLANK");
        return false;
    }
     if (this.mArrGrid.length>0 && (CustomValidator.bmgfNullCheckValidator(this.mArrGrid[this.mnumGridRowIndex]['GroupName']))) {
                this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.GROUPNAMECANNOTBEBLANK");
            return false;
    } 
    if (this.mArrGrid.length==0) {
        this.bmlfOpenErrorBlock("CUSTOMMASTERDESIGN.MSG.PLEASEADDATLEASTONEROW");
    return false;
} 
    
    else {
        return true;
    }
}

