  bmlfSaveProductData(): void {

      
    if(this.marrSetupDetailMainList.length == 0){
        this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.INSERTGRID'));
        return;
    }
    this.marrSetupDetailMainList.forEach(element => {
        element.TableName = this.mobjcustomMasterHeaderModel.TableName
    });
           


      this.mbIsLoading = true;
      let parrHeaderData = [];
      let DetailData =[];
      this.mobjcustomMasterHeaderModel.RecUserID= this.mobjUserData.UserId;
      parrHeaderData.push(Object.assign({}, this.mobjcustomMasterHeaderModel));
      let pobjJData = {
          HeaderData: parrHeaderData,
          DetailData :this.marrSetupDetailMainList,
          Response: [{
              "ResponseText": "",
              "ErrorCode": ""
          }]
      };
      let pobjJson = {
          JSON: JSON.stringify(pobjJData)
      };

      this.formService.bmlfSaveCustomMasterRecord(pobjJson, this.msFormMode).subscribe(
          res => {
              this.mbIsLoading = false;
              if (res.Response[0].ErrorCode == "") {
                  this.FieldExtender.mbChange = false;
                  this.customCtrl.mbChange = false;
                  this.mbIsChangeData = false;
                  if (this.msFormMode == "I") {
                      this.msFormMode = "U";
                      this.msCopyRecord = "N";
                      this.mobjcustomMasterHeaderModel.TableName = res.HeaderData[0].TableName;
                      this.msPrdKey = this.mobjcustomMasterHeaderModel.TableName;
                      this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.SAVE"));
                  } else if (this.msFormMode == "U") {
                      if(this.msIsDetail == true){
                        this.msPrdKey = res.HeaderData[0].TableName;
                      }
                      this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate("COMMON.MSG.EDIT"));
                  }
                  // FieldExtender -- start update
                  let screenKeyValue = this.msScreenName + "  " + this.msPrdKey;
                  let jsonData = this.FieldExtender.bmlfSaveData(screenKeyValue);
                  if (jsonData.length > 0) {
                      this.FieldExtender.bmlfCallApi(jsonData).then(res => {
                          if (res != null) { }
                      });
                  };
                  // FieldExtender -- End update
                  if (this.mbIsSearchMode) {
                      this.mbIsSearchMode = false;
                      this.bmlfOpenLookupModal('MasterList', this.msScreenName, 'MasterList', 'click', '', 'custom', 'cmdSearch', 'CUSTOMMASTERDESIGN.TITLE');
                  }else if(this.mbIsStatusChange){
                    this.mbIsStatusChange = false;
                    this.bmlfFooterActionFn("table");
                  } else if (this.mbIsAddMode) {
                      this.mbIsAddMode = false;
                      this.bmlfRefreshForm();
                  } else if (this.mbIsCopyMode) {
                      this.mbIsCopyMode = false;
                      this.bmlfCopyData();
                  } else if (this.mbIsCloseMode) {
                      this.mbIsCloseMode = false;
                      this.router.navigate(["/main/customization/custom-master-design"]);
                  } else {

                    if(res.HeaderData[0].dbTableName.includes('BME_CUSTH_')){
                        this.bmlfGetCustomDesignType("Master",this.msPrdKey);
                    }else{
                        this.bmlfGetCustomDesignType("detail",this.msPrdKey);
                    }

                    //   if(this.msIsDetail == true){
                    //     this.bmlfGetCustomDesignType("detail",this.msPrdKey);
                    //   }else{
                    //     this.bmlfGetCustomDesignType("Master",this.msPrdKey);
                    //   }
                  }
              } else {
                  this.mbIsSearchMode = false;
                  this.mbIsAddMode = false;
                  this.mbIsCopyMode = false;
                  this.mbIsDeleteMode = false;
                  this.mbIsCloseMode = false;
                  this.toastMessageService.notifyError(res.Response[0].ResponseText)
              }
              console.log(res);
          }
      ).add(() => { this.mbIsLoading = false; })
  }

