  // So Footer Action finction
  bmlfFooterActionFn(event) {
      if (event == 'searchEvn') {
          if (this.mbIsChangeData || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
              this.mbSaveDialogView = true;
              this.mbIsSearchMode = true
              return false;
          }
          this.bmlfOpenLookupModal('MasterList', this.msScreenName, 'MasterList', 'click', '', 'custom', 'cmdSearch', 'CUSTOMMASTERDESIGN.TITLE');
      } else if (event == 'addEvn') {
          if (!this.utilService.mbWrite) {
              this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
              return false;
          };
          if (!this.bmlfValidData()) {
              return;
          }
          if (this.mbIsChangeData || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
              this.mbSaveDialogView = true;
              this.mbIsAddMode = true
              return false;
          }
          this.bmlfRefreshForm();
      } else if (event == 'deleteEvn') {
          if (!this.utilService.mbDelete) {
              this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
              return false;
          };
          if (this.mobjcustomMasterHeaderModel.TableName == '' && this.msFormMode == "I" && this.msCopyRecord == "N" && !this.mbIsChangeData && this.FieldExtender.mbChange == false && this.customCtrl.mbChange == false) {
              this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
              return false;
          }
          // if (this.msPrdKey == '' && this.msCopyRecord == "N" && !this.mbIsChangeData && this.FieldExtender.mbChange == false && this.customCtrl.mbChange == false) {
          //     this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.MSG.NODATADELETE'));
          //     return false;
          // }
          if (this.mobjcustomMasterHeaderModel.TableName == '' && this.msFormMode == "I" && this.msCopyRecord == "Y" && !this.mbIsChangeData && this.FieldExtender.mbChange == false && this.customCtrl.mbChange == false) {
              this.bmlfRefreshForm();
              return false;
          }


          this.msIsConformDelete =true;

         // this.mbShowDeleteDialog = true;
      } else if (event == 'copyEvn') {
          if (!this.utilService.mbWrite) {
              this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
              return false;
          };
          if (CustomValidator.bmgfNullCheckValidator(this.mobjcustomMasterHeaderModel.TableName)) {
              this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate("COMMON.MSG.COPY"));
              document.getElementById("txtPrdKey").focus();
              return;
          }
          if (this.mbIsChangeData || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
              this.mbSaveDialogView = true;
              this.mbIsCopyMode = true;
              return false;
          }
          this.bmlfCopyData();
      }else if(event == 'table'){

        if (!this.bmlfValidData()) {
            return;
        }

        if(this.marrSetupDetailMainList.length <= 0){
            this.toastMessageService.notifyError(this.utilService.bmgfonTranslate("CUSTOMMASTERDESIGN.MSG.INSERTDETAILTABLE"));
            return;
        }

        if (this.mbIsChangeData || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true) {
            this.mbSaveDialogView = true;
            this.mbIsStatusChange = true;
            return false;
        }
        this.bmlfOnStatusChange();
      }
  }



