    // Add Line Item Grid 1
    bmlfonAddLineToGrid() {
        debugger
        if (!this.utilService.mbWrite) {
            this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
            return false;
        };


        const pobjDataCopy: any = Object.assign({}, this.mObjDetailModelBlank);
        let piLastIndex;
        piLastIndex = this.mArrGrid.length;
        this.mnumGridRowIndex=this.mArrGrid.length;

        if (piLastIndex > 0) {
            let index = this.mArrGrid.length - 1;
            if (this.isEmpty(this.mArrGrid[index]['GroupName'])) {
                this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('CUSTOMMASTERDESIGN.MSG.GROUPNAME')) + " " + (index + 1).toString());
                let eleID = 'txtValue' + (index).toString();
                setTimeout(function () { document.getElementById(eleID).focus(); }, 10);
            } else {
                pobjDataCopy.Flag = "A";
                pobjDataCopy.RowNum = this.mArrGrid.length;
                pobjDataCopy.RecuserID = this.mobjUserData.UserId;
                this.mnumGridRowIndex=pobjDataCopy.RowNum;
                
                this.mArrGrid.push(pobjDataCopy);
                let eleID = 'txtValue' + (piLastIndex).toString();
                setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
            }
        } else {
            pobjDataCopy.Flag = "A";
            pobjDataCopy.RowNum = piLastIndex;
            this.mnumGridRowIndex=pobjDataCopy.RowNum;
            pobjDataCopy.RecuserID = this.mobjUserData.UserId;
            this.mArrGrid.push(pobjDataCopy);
            let eleID = 'txtValue' + (piLastIndex).toString();
            setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
        }
       
    }
