    bmlfOnChangeType(value) {
        this.mbIsDisabledModule = false;
        this.mbIsDisabledLeftPane = false;
        this.mbIsDisabledGrid = false;
        this.mbIsChangeModalData=true;
        if (value == 'Dialogue') {
            this.mbIsDisabledModule = true;
            this.mbIsDisabledLeftPane = true;
            this.mObjModel.ParentMenu = '';
        } 
        else if(value=='Sample Project'){
            this.mObjModel.ParentMenu='Sample Management';
            this.mbIsDisabledModule = true;
        }
        else if(value=='CRM'){
            this.mObjModel.ParentMenu='Sales Opportunity Transaction';
            this.mbIsDisabledModule = true;
        }

        else if (value == 'Sample Project' || value == 'Sample Request') {
            this.mbIsDisabledModule = true;
            this.mObjModel.ParentMenu = '';
        }
        else if (value == 'Screen') {
            this.mObjModel.ParentMenu = '';
        }
        
    }
